/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #333; */
  background-color: black;
  line-height: 1.5;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a {
  color: #fff;
  text-decoration: none;
}

*,
::before,
::after {
  box-sizing: border-box;
}

#root {
  min-height: 100vh;
  position: relative;
}

.container {
  min-height: 100vh;
  /* margin-bottom: 60px; */
  width: 100%;
  max-width: 320px;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

footer {
  padding: 16px 0;
  height: 60px;
  width: 100%;
  display: flex;
  justify-content: center;
  /* position: fixed; */
  bottom: 0;
  color: #fff;
}

.social-btn {
  padding: 6px 8px;
  border-radius: 20px;
  display: flex;
  align-items: center;
  font-size: 0.625rem;
  font-weight: bold;
  color: black;
  cursor: pointer;
}
.social-btn > span {
  margin-right: 4px;
}
.social-btn > svg {
  font-size: 1.25rem;
}

.auth-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  grid-gap: 8px;
  gap: 8px;
}
.auth-form > span {
  padding: 4px;
  display: inline-flex;
  justify-content: center;
  color: red;
  font-size: 0.875rem;
}

.formInput {
  width: 100%;
  padding: 12px 24px;
  border-radius: 20px;
  /* border: 1px solid black; */
  background-color: white;
  color: black;
}
.formInput::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0.6);
}
.formInput::placeholder {
  color: rgba(0, 0, 0, 0.6);
}

.formBtn {
  cursor: pointer;
  width: 100%;
  padding: 12px 24px;
  text-align: center;
  color: white;
  border-radius: 20px;
  background-color: #04aaff;
  outline: 0;
  border: 0;
  cursor: pointer;
}

.toggle-link {
  margin: 8px auto;
  padding: 12px 24px;
  font-size: 0.875rem;
  color: #04aaff;
  text-align: center;
  cursor: pointer;
}

.tweet {
  padding: 8px;
  border: 1px solid #536471;
  color: #fff;
  border-top: 0;
  /* margin-bottom: 4px; */
}
.tweet-name{
  display: flex;
  align-items: baseline;
}
.tweet-name h3{
  font-weight: bold;
  font-size: 1.25rem;
}
.tweet-name span{
  margin-left: 0.25rem;
  margin-right: 0.25rem;
  font-size: 0.875rem;
  color: gray;
  white-space: inherit;
}
.tweet > h4 {
  font-size: 0.875rem;
  padding: 0.5rem 0;
}
.tweet img{
  border-radius: 1rem;
}
.tweet-btn-group{
  display: flex;
  justify-content: flex-end;
}
.tweet-btn{
  margin: 0 0.25rem;
  padding: 0.25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: 0;
  outline: none;
  font-size: 1.5rem;
}

.edit-tweet{
  border: 0;
  outline: 0;
  background-color: transparent;
  color: white;
  padding: 4px;
  border-bottom: 1px solid silver;
}
.tweet-form {
  padding: 8px;
  border: 1px solid #536471;
  border-top: 0;
}
.form-input {
  padding: 16px 8px;
}
.form-input > input {
  background-color: transparent;
  border: 0;
  outline: 0;
  font-size: 1rem;
  color: #fff;
}
.form-footer {
  padding: 8px 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #536471;
}
.form-footer > div {
  display: flex;
  align-items: center;
}
.upload-btn {
  display: inline-flex;
  font-size: 1.5rem;
  color: #fff;
  cursor: pointer;
}

.tweet-btn {
  padding: 6px 16px;
  border-radius: 20px;
  font-size: 1rem;
  outline: 0;
  border: 0;
  background-color: #04aaff;
  color: #fff;
  cursor: pointer;
}

.tweet-photo{
  padding: 0.5rem;
  position: relative;  
}

.tweet-photo img{
  border-radius: 1rem;
}

.tweet-photo button{
  position: absolute;
  top: 1rem;
  left: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1rem;
  border-radius: 50%;
  background-color: ivory;
  border: 0;
  outline: none;
  cursor: pointer;
}
.profile-container{
  border: 1px solid #536471;
  border-top: 0;
  color: #fff;
}
.profile-header{
  padding: 1rem 0.5rem;
  display: flex;
  justify-content: space-between;
  color: #fff;
}
.display-name{
  padding: 0.5rem;
}
.profile-btn{
  background-color: transparent;
  color: #fff;
  border: 1px solid #536471;
  cursor: pointer;
  padding: 4px 8px;
  border-radius: 1rem;
}
.profile-btn:hover{
  background-color: #8394a0;
  color: black;
}
.profile-edit-row{
  padding: 1rem 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.profile-edit-input{
  padding: 0.25rem;
  font-size: 1rem;
  color: #fff;
  background-color: transparent;
  outline: none;
  border: 0;
  border-bottom: 1px solid silver;
}
.nav-container{
  border-left: 1px solid #536471;
  border-right: 1px solid #536471;
}
.nav-title{
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  color: #fff;
  font-size: 1.25rem;
}
.nav-title button{
  background-color: transparent;
  color: #fff;
  border: 0;
  outline: none;
  font-size: 0.75rem;
  font-weight: bold;
  cursor: pointer;
}
.nav-title button:hover{
  text-decoration: underline;
}
.nav-nav {
  max-width: 320px;
}

.nav-ul {
  display: flex;
  font-size: 2rem;
  border-bottom: 1px solid #536471;
}

.nav-ul > li {
  padding-top: 0.5rem;
  width: 50%;
  cursor: pointer;
}

.nav-ul > li:hover {
  background-color: rgba(231, 233, 234, 0.1);
}

.nav-ul a{
  padding-bottom: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* .nav-ul svg{
  padding-bottom: 5px;
} */

.selected {
  border-bottom: 5px solid dodgerblue;
}
